@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:100,100i,300,300i,400,400i,600,600i,700,700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Eczar&display=swap);
@import url(https://fonts.googleapis.com/css?family=Yeseva+One&display=swap);
@import url(https://use.typekit.net/tsd0bbh.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Default settings */

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background-color: #161616;
  color: #fff;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

main {
  min-height: 100vh;
  display: grid;
  width: 100vw;
  padding: 10vh 0;
 }

h1 {
  font-family: "Eczar", serif;
  display: grid;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: 100;
  color: #fff;
  margin-bottom: 1rem;
}

p {
  font-family: "Josefin sans", sans-serif;
}

a {
  color: #fff;
  text-decoration: none;
}

button {
  width: 150px;
  height: 50px;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.2) 1px 1px 2px;
  font-family: "Josefin sans";
  text-transform: uppercase;
  padding: 20px;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 0.6rem;
  background-color: #161616;
  border: 1px solid #fff;
  color: #fff;
}

.icons {
  -webkit-transform: scale(2);
          transform: scale(2);
  cursor: pointer;
}

.projects-grid {
  display: grid;
  grid-gap: 5rem;
  gap: 5rem;
  width: 100%;
  padding: 0 2rem;
}

.add {
  position: fixed;
  width: 60px;
  height: 60px;
  right: 3vw;
  top: 80vh;
  border-radius: 50%;
  background: #161616;
  border: 2px solid #fff;
  display: grid;
  place-items: center;
  box-shadow: rgba(0, 0, 0, .4) 1px 3px 10px;
  z-index: 5;
}

.admin-icons {
  position: absolute;
  right: 2rem;
  bottom: 1rem;
  display: grid;
  grid-auto-flow: column;
  justify-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  cursor: pointer;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  color: #fff;
}

@media (max-width: 1000px) {
  .project {
    min-width: 100vh;
  }
}

.project {
  display: grid;
  /* grid-template-columns: 1fr; */
  place-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  height: 100vh;
  position: relative;
  width: 100vw;
  color: #161616;
  padding: 10vh 10rem;
  scroll-snap-align: center;
  scroll-snap-stop: normal;
}

/* .project:hover {
  animation: test 5s ease infinite;
}

@keyframes test {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(0px);
  }
} */

.project:nth-child(even) .project-info {
  grid-row-start: 1;
}

.project .byline{
  margin-bottom: 1rem;
}

.project .project-overview-img {
  width: 80%;
  height: auto;
  object-fit: cover;
  align-self: center;
  justify-self: end;
  box-shadow:  0px 10px 30px -5px rgba(0, 0, 0, .3);
  cursor: pointer;
}

.project .display-title {
  font-family: 'Yeseva One', cursive;
  /* font-family: "Eczar", serif; */
  position: absolute;
  font-size: 5rem;
  color: #fff;
  top: 25vh;
  left: 10vw;
  width: 5rem;
}

.project .view-project {
  position: absolute;
  top: 85vh;
  right: 10vw;
  opacity: 0.5;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.project .view-project:hover {
  opacity: 1;
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

.project .admin-icons {
  position: absolute;
  right: 5vw;
  bottom: 5vh;
  display: grid;
  grid-auto-flow: column;
  justify-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  cursor: pointer;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  color: #fff;
}

@media (max-width: 900px) {
  /* .project {
    max-width: 100vh;
  } */

  .project .display-title {
    font-size: 4rem;
    color: #fff;
    top: 15rem;
    left: 0;
  }

}

.introPage {
  display: grid;
  place-items: start;
  align-content: start;
  margin-left: 10rem;
}

.introPage h1 {
  color: #fff;
  font-size: 4rem;
  justify-content: start;
  font-family: 'eskapade-fraktur', serif;
}
.introPage p{
  color: #fff;
  font-size: 2rem;
  justify-content: start;
  font-family: 'eskapade-fraktur', serif;
  padding-top: 1rem;
}

.introPage .name {
  color: #fff;
  /* color: rgb(133, 12, 12); */
  font-size: 5rem;
}


.introPage.name:hover {
  -webkit-transform: none;
          transform: none;
  cursor: pointer;
}

.introPage p:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

h3 {
  color: #fff;
}

a {
  color: #fff;
}

@media (max-width: 1000px) {
  .introPage p {
    font-size: 2.5rem;
  }
}

h4 {
  font-weight: 100;
}
.header-container {
  position: fixed;
  width: 100vw;
  height: 10vh;
  display: grid;
  place-items: end;
  z-index: 2;
}

.header-container header a {
  font-size: 1.3rem;
  text-decoration: none;
  color: #fff;
  font-family: 'eskapade-fraktur', serif;
  justify-items: end;

}
.header-container header p {
  font-size: 1.3rem;
  font-weight: 100;
  color: #fff;
  font-family: 'eskapade-fraktur', serif;
  justify-content: start;
  margin-left: 2rem;
  margin-right: 2rem;

}

.burger-menu {
  display: none;
  z-index: 2;
}

header {
  width: 100vw;
  height: 10vh;
  display: grid;
  grid-template-columns:  1fr 1fr;
  justify-content: start;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  z-index: 1;
  z-index: 3;
}

.header-right-items {
  display: grid;
  grid-auto-flow: column;
  place-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  margin-right: 2rem;
  align-items: center;
}

[aria-current='page'], .active {
  font-weight: bold;
}

@media (max-width: 1000px) {

  .header-container {
    place-items: center;
  }
   
  header {
    -webkit-transform: translateY(-150%);
            transform: translateY(-150%);
    grid-auto-flow: row;
    align-content: center;
    min-height: 100vh;
    -webkit-transition: .7s ease;
    transition: .7s ease;
    align-items: center;
    justify-content: center;
    background: #161616;
  }

  header h1 {
    margin: 0;
  }

  .burger-menu {
    display: block;
    cursor: pointer;
    height: 10vh;
  }

  header.a {
    font-size: 1.3rem;
  }

  header.visible {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  main {
    padding: 2rem;
  }
}
.edit {
  place-items: center;
}

input, textarea, label {
  font-size: 1rem;
  padding: .5rem;
  display: block;
  margin-bottom: 3rem;
}


.checkboxes {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

textarea {
  min-width: 50vw;
  min-height: 20vh;
}

.project-images {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 3rem;
}

.project-images > div {
  position: relative;
  display: grid;

}

.project-images img {
  max-height: 250px;
  width: 100%;
  object-fit: cover;
}

.imageNameIndicator {
  text-transform: uppercase;
  font-size: 1rem;
}

select {
  margin-bottom: 3rem;
}

.edit img {
  width: 20rem;
}

.edit label {
  padding: 0;
}

.uploadButton {
  width: 120px;
  height: 30px;
  font-family: 'Josefin sans';
  text-transform: uppercase;
  padding: 10px;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: .6rem;
  border: 1px solid #fff;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, .2) 1px 1px 2px;
  margin-top: 1rem;
}
.project-details {
  padding: 5rem 15rem;
}

.project-details .project-details-content {
 display: grid;
 grid-gap: 2rem;
 gap: 2rem;
}

.project-details .project-details-content .edit-button {
  position: absolute;
  font-size: 1.5rem;
  top: 25vh;
  left: 10vw;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  opacity: 0.5;
}
.project-details .project-details-content .back-button {
  position: absolute;
  font-size: 1.5rem;
  top: 15vh;
  left: 10vw;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  opacity: 0.5;
}

.project-details .project-details-content .back-button:hover {
  position: absolute;
  top: 15vh;
  left: 10vw;
  opacity: 1;
  -webkit-transform: translateX(-2px);
          transform: translateX(-2px);
}

.project-details .project-details-content .info-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.project-details .project-details-content .project-detail-title {
  font-size: 4rem;
  font-family: 'eskapade-fraktur', serif;
  font-family: 'Yeseva One', cursive;
  margin-bottom: 7rem;
  -webkit-animation: fadeIn 1.5s ease .5s;
          animation: fadeIn 1.5s ease .5s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.project-details-content .description {
  width: 400px;
  margin-bottom: 5rem;
  -webkit-animation: fadeIn 2s ease .75s;
          animation: fadeIn 2s ease .75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.project-details-content .year {
 justify-self: end;
 -webkit-animation: fadeIn 1.5s ease .75s;
         animation: fadeIn 1.5s ease .75s;
 -webkit-animation-fill-mode: backwards;
         animation-fill-mode: backwards;
}

.project-details-content img{
  width: 100%;
  box-shadow:  0px 10px 30px -5px rgba(0, 0, 0, .3);
}

.project-details .project-details-img-parallax {
  box-shadow:  none;
  width: 80%;
  -webkit-animation: fadeIn 1.5s ease 1s;
          animation: fadeIn 1.5s ease 1s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.pager {
  background: #161616;
  text-decoration: none;
  width: 100vw;
  height: 10vh;
  margin-top: 10vh;
  display: grid;
  grid-auto-flow: column;
  z-index: 5;
}

.pager .navigate-projects {
  display: grid;
  grid-auto-flow: column;
  place-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 4rem;
}

.navigate-projects .prev {
  margin-left: 4rem;
  font-size: .9rem;
  justify-self: end;
  opacity: 0.5;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.navigate-projects .divider {
  opacity: 0.5;
}

.navigate-projects .next {
  margin-right: 4rem;
  font-size: .9rem;
  justify-self: end;
  opacity: 0.5;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.navigate-projects .prev:hover {
  opacity: 1;
  -webkit-transform: translateX(-2px);
          transform: translateX(-2px);
}

.navigate-projects .next:hover {
  opacity: 1;
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

@media (max-width: 700px) {
  .project-details {
    display: grid;
    justify-content: center;
    padding: 0 5rem;
  }
} 
